.d_popapVideoWrapperFull {
  display: flex;
  justify-content: center;
  align-items: center;
}

.d_popapVideo {
  position: fixed;
  top: 100px;
  left: 0;
  bottom: 0;
  right: 0;
}

.d_popapVideo__closer {
  top: 0;
  position: absolute;
  top: 35px;
  right: 25px;
  width: 40px;
  height: 40px;
  background-color: transparent;
  border: none;
}

.d_popapVideo__closer:before,
.d_popapVideo__closer:after {
  content: "";
  position: absolute;
  top: 50%;
  display: block;
  width: 100%;
  border-bottom: 8px solid #134859;
  border-radius: 20%;
  cursor: pointer;
}

.d_popapVideo__closer:before {
  transform: rotate(45deg);
}

.d_popapVideo__closer:after {
  transform: rotate(-45deg);
}

.d_popapVideoWrap {
  width: 77%;
  z-index: 1000;
}

.d_modal-video__width {
  width: 100%;
  padding: 67px 100px;
}

.d_modal-video__height {
  width: 100%;
  padding-top: 56.25%;
  position: relative;
}

.d_modal-video__height iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

@media screen and (max-width: 950px) {
  .d_modal-video__width {
    padding: 10px;
  }
}

@media screen and (max-width: 567px) {
  .d_popapVideoWrap {
    width: 100%;
  }
}